header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 3; }

.hamburger {
  display: none; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    outline: none;
    text-decoration: none; }

  .menu_item {
