@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 50%;
        height: 100%;
        top: 0;
        left: -100%;
        background-color: rgba(0,0,0,0.8);
        &_active {
            left: 0; }
        .menu_item {
            margin-bottom: 20px;
            border: none;
            .menu_link {
                font-size: 20px; } } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.6s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-last-child(1) {
                    transform: translateY(2px) rotate(-45deg); }
                &:nth-last-child(2) {
                    display: none; }
                &:nth-last-child(3) {
                    transform: translateY(2px) rotate(45deg); } } } }
    .subheader {
        .xs-hidden {
            display: none; }
        .xs-visible {
            display: block; }
        .subheader_logo {
            img {
                width: 100%; } }
        .subheader_phone {
            display: block;
            margin-top: 10px;
            color: #fff;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        .subheader_button {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }
    .mobile {
        .title {
            font-size: 31px; }
        .item {
            margin: 0 auto;
            margin-top: 10px;
            width: 400px; } }
    .choise {
        .choise_img {
            width: 510px; } }
    .footer {
        &_social {
            width: 20%;
            &_link {
                width: 131px;
                margin-left: 5px; } }
        &_items {
            .footer_center {
                width: 56%;
                padding-left: 32px;
                .blue_block {
                    width: 235px; } } }
        .footer_download {
            &_link {
                    width: 140px;
                    margin-right: 5px;
                    &_1 {
                        img {
                            width: 45px; }
                        a {
                            width: 45px; } }
                    &_2 {
                        img {
                            width: 45px; }
                        a {
                            width: 45px; } }
                    &_3 {
                        img {
                            width: 45px; }
                        a {
                            width: 45px; } } } } } }
